import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable, Subject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class SettingsService {

  private readonly subdivisions: Subject<number>;

  constructor() {
    this.subdivisions = new BehaviorSubject(0);
    this.subdivisions.asObservable().subscribe(value => console.log('value:' + value));
  }

  setSubdivisions(subdivisions: number) {
    this.subdivisions.next(subdivisions);
  }

  getSubdivisionObservable(): Observable<number> {
    return this.subdivisions;
  }
}
