import { Node } from './node';

export class Edge {

  constructor(readonly nodeA: Node,
              readonly nodeB: Node) {
  }

  containsNode(node: Node) {
    return this.nodeA === node || this.nodeB === node;
  }

  getOtherEnd(node: Node) {
    if (node === this.nodeA) {
      return this.nodeB;
    }
    return this.nodeA;
  }

  connects(nodeA: Node, nodeB: Node): boolean {
    return (this.nodeA === nodeA && this.nodeB === nodeB)
      || (this.nodeA === nodeB && this.nodeB === nodeA);
  }
}
