import { Node } from './primitive/node';
import { Edge } from './primitive/edge';
import { Quadrangle } from './face/quadrangle';
import { Mesh } from './mesh';
import { Face } from './face/face';

export class Scene {

  readonly meshes: Mesh[];

  constructor();

  constructor(meshes: Mesh[]);

  constructor(meshes?: Mesh[]) {
    const nodeA = new Node(10, 10);
    const nodeB = new Node(990, 10);
    const nodeC = new Node(990, 990);
    const nodeD = new Node(10, 990);

    const points: Node[] = [nodeA, nodeB, nodeC, nodeD];

    const edges = [
      new Edge(nodeA, nodeB),
      new Edge(nodeB, nodeC),
      new Edge(nodeC, nodeD),
      new Edge(nodeD, nodeA)
    ];

    const face: Face[] = [new Quadrangle(points, edges)];
    if (!meshes) {
      this.meshes = [new Mesh(points, edges, face)];
    } else {
      this.meshes = meshes;
    }
  }

  subdivideMeshes(): Scene {
    return new Scene(this.meshes.map(mesh => mesh.subdivide()));
  }
}
