import { Component, OnInit } from '@angular/core';
import { Scene } from '../domain/scene';
import { SettingsService } from '../settings/settings.service';
import { map } from 'rxjs/operators';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-graph',
  templateUrl: './graph.component.html',
  styleUrls: ['./graph.component.scss']
})
export class GraphComponent implements OnInit {

  readonly baseScene: Scene;
  readonly currentScene: Observable<Scene>;

  constructor(private settings: SettingsService) {
    this.baseScene = new Scene();
    this.currentScene = settings.getSubdivisionObservable()
      .pipe(
        map(subdivisions => GraphComponent.subdivideScene(this.baseScene, subdivisions))
      );
  }

  static subdivideScene(scene: Scene, divisions: number): Scene {
    if (divisions <= 0) {
      return scene;
    }
    return this.subdivideScene(scene, divisions - 1).subdivideMeshes();
  }

  ngOnInit() {
  }

}
