import { Face } from './face';
import { Node } from '../primitive/node';
import { Edge } from '../primitive/edge';

export class Quadrangle implements Face {

  readonly centroid: Node;

  constructor(readonly nodes: Node[],
              readonly edges: Edge[]) {
    this.centroid = Quadrangle.getCentroid(nodes);
  }

  private static getCentroid(nodes: Node[]): Node {
    const centerX = nodes.map(node => node.posX).reduce((sum, current) => sum + current) / nodes.length;
    const centerY = nodes.map(node => node.posY).reduce((sum, current) => sum + current) / nodes.length;
    return new Node(centerX, centerY);
  }
}
