import { Face } from './face/face';
import { Edge } from './primitive/edge';
import { Node } from './primitive/node';
import { Quadrangle } from './face/quadrangle';

export class Mesh {

  constructor(readonly nodes: Node[],
              readonly edges: Edge[],
              readonly faces: Face[]) {
  }

  private static subdivideEdge(edge: Edge): { centroid: Node; edgeA: Edge; edgeB: Edge } {
    const centerX = (edge.nodeA.posX + edge.nodeB.posX) / 2;
    const centerY = (edge.nodeA.posY + edge.nodeB.posY) / 2;
    const centroid: Node = new Node(centerX, centerY);
    const edgeA = new Edge(edge.nodeA, centroid);
    const edgeB = new Edge(centroid, edge.nodeB);
    return {centroid, edgeA, edgeB};
  }

  subdivide(): Mesh {
    const edgeMap: Map<Face, Edge[]> = new Map<Face, Edge[]>();
    const nodeMap: Map<Face, Node[]> = new Map<Face, Node[]>();
    const faceMap: Map<Edge, Face[]> = new Map<Edge, Face[]>();
    const centroidMap: Map<Face, Node> = new Map<Face, Node>();
    this.edges.forEach(edge => faceMap.set(edge, []));

    this.faces.forEach(face => {
      edgeMap.set(face, []);
      nodeMap.set(face, [...face.nodes, face.centroid]);
      centroidMap.set(face, face.centroid);
      face.edges.forEach(edge => faceMap.get(edge).push(face));
    });

    const nodes = this.nodes.concat(this.faces.map(face => face.centroid));
    const edges: Edge[] = [];
    this.edges.forEach(edge => {
      const affectedFaces = faceMap.get(edge);
      const subdivided = Mesh.subdivideEdge(edge);
      nodes.push(subdivided.centroid);
      edges.push(subdivided.edgeA);
      edges.push(subdivided.edgeB);
      affectedFaces.forEach(face => {
        nodeMap.get(face).push(subdivided.centroid);
        edgeMap.get(face).push(subdivided.edgeA);
        edgeMap.get(face).push(subdivided.edgeB);
        const faceCentroid = centroidMap.get(face);
        const centroidEdge = new Edge(subdivided.centroid, faceCentroid);
        edges.push(centroidEdge);
        edgeMap.get(face).push(centroidEdge);
      });
      return subdivided;
    });

    // Connect the faces
    const faces: Quadrangle[] = this.faces.map(face => {
      const faceNodes: Node[][] = face.nodes.map(node =>
        edgeMap.get(face).filter(edge => edge.containsNode(node))
          .map(edge => edge.getOtherEnd(node))
          .concat(centroidMap.get(face))
          .concat(node)
      );
      return faceNodes.map((n: Node[]) => {
        const faceEdges: Edge[] = [];
        for (let i = 0; i < n.length; i++) {
          for (let j = 0; j < n.length; j++) {
            faceEdges.push(...edges.filter(e => e.connects(n[i], n[j])));
          }
        }
        return new Quadrangle(n, Array.from(new Set(faceEdges)));
      });
    }).reduce((collection, f) => collection.concat(f));

    return new Mesh(nodes, edges, faces);
  }


}
